// }

const BASE_URL = 'https://w0uosqj8g0.execute-api.us-east-1.amazonaws.com/dev/search/{}';
class SearchBox extends Component{
	constructor(props){
		super(props);
		this.onSubmit = this.onSubmit.bind(this);
		this.toggleWorking = this.toggleWorking.bind(this);
		this.onDataArrived = this.onDataArrived.bind(this);
	}
	state={
		query:"",
		working:false,
	}
	toggleWorking(){
		var {working} = this.state;
		working = !working;
		this.setState({...this.state,working:working});
	}
	onDataArrived(data){
		if(this.state.working)this.toggleWorking();
		// console.log(data);
		this.props.dispatch(showTweets(data.statuses));
	}
	onSubmit(event){
		var {working,query} = this.state;
  		if(working===false){
  			this.toggleWorking();
  			// this.setState({...this.state,working:true});
  			if(query!=""){
  				var url = format(BASE_URL,query);
  				// console.log(query," --> " , url);
  				function proccess(items){
  					// this.setState({...this.state,working:false})
  					console.log(items);
  				}
  				var tweets = fetch(url)
  				.then((result)=>(result.json()))
  				.then(items=>this.onDataArrived(items));
				// this.toggleWorking();
  			}
  		}
	}
	render(){
		return (
			<div style={{margin:10,width:500}}>
				<InputGroup>
		          <Input placeholder={SEARCH_KEYWORD} onChange={(event) => (
		          		this.setState({query:event.target.value,working:this.state.working})
		          	)} />
		          <InputGroupAddon addonType="append"><Button disabled={this.state.working} onClick={(event)=>(this.onSubmit(event))} 
		          	color="primary">{SEARCH_BUTTON}</Button></InputGroupAddon>
		        </InputGroup>
		    </div>
		);
	}
}
// let SearchBox = ({dispatch}) => {

