import React, { Component } from 'react';
import './App.css';
import {
  InputGroup,
  InputGroupAddon,
  Input,
  Button,
 } from 'reactstrap';
import GoogleMapReact from 'google-map-react';
import { connect } from 'react-redux';
import SearchBox from './components/SearchBox';
import Tweet from './components/Tweet';
import TweetList from './components/TweetList';
import randomLocation from 'random-location'

// TODO: Should be ommited!!!
const GOOGLE_MAP_KEY = (!process.env.NODE_ENV || process.env.NODE_ENV === 'development') ? "AIzaSyAPcDa3xgIACldjPxgQNfIpvK-OrrPeQWQ" : "AIzaSyAUnr5kDABtuQ_f0L3jLh-X1hu1Sqtj39Y";

// const AnyReactComponent = ({ text }) => <Button color="primary">{SEARCH_BUTTON}</Button>;

class App extends Component {
  
  static defaultProps = {
    center: {lat: 32.4279, lng: 53.6880},
    zoom: 1,
  };

  render() {
    const options = {
      fullscreenControl: true, 
    };
    // let {tweets} = this.props;
    const P = {
      latitude: 37.7768006,
      longitude: -122.4187928
    }
     
    const R = 500000 // meters
 
    const randomPoint = randomLocation.randomCirclePoint(P, R)
    let tweets = this.props.tweets.map((tweet, index) => (
            <Tweet key={index} lat={randomLocation.randomCirclePoint(P, R).latitude} lng={randomLocation.randomCirclePoint(P, R).longitude} tweet={tweet} onClick={(e) => console.log(e)} />
    ));
            // <Tweet key={index} lat={tweet.location.lat} lng={tweet.location.lng} tweet={tweet} onClick={(e) => console.log(e)} />

          // {tweets!== undefined && tweets.map(function(tweet, index){
          //    return (<Tweet lat={50} lng={50} key={index} tweet={tweet} onClick={(e) => console.log(e)} />)
          // })}
    // console.log("Tweets: " ,tweets);
    return (
      <div style={styles.container} className="App">
        <GoogleMapReact
          bootstrapURLKeys={{
            key:GOOGLE_MAP_KEY,
            language: 'en',
          }}
          options={options}
          style={styles.map}
          center={this.props.center}
          zoom={this.props.zoom}>
          {tweets}
        </GoogleMapReact>
        <SearchBox style={styles.search}/>
      </div>
    );
  }
}
          // <TweetList/>

const styles = {
  container:{
    flex:1,
    display: 'flex', 
    justifyContent: 'center',
  },
  search:{
    width:500,
    height:100,
    flex:1,
 
  },
  map:{
    flex:1,
  }
};

const mapStateToProps = state => {
  return {
    tweets: state.tweets,
  }
}

App = connect(mapStateToProps)(App);
export default App;



